import React from 'react'
import {Link} from 'react-router-dom'

const MobileNav = (props) => {

    let mobileNavClasses = ['container-links'];
    if (props.show){
        mobileNavClasses = 'container-links slide';
    }
    return (
        <div id="mobile-links" className={mobileNavClasses}>
            <ul>
                <li><Link className="mobile-nav-link" to="/info">INFO</Link></li>
                <li><Link className="mobile-nav-link" to="/galeria">GALERIA</Link></li>
                <li><Link className="mobile-nav-link" to="/eventos">EVENTOS</Link></li>
                <li><Link className="mobile-nav-link" to="/pavilhoes">ESPAÇOS</Link></li>
                {/* <li><Link to="/reservas">SERVIÇOS</Link></li> */}
                <li><Link className="mobile-nav-link" to="/noticias">NOTÍCIAS</Link></li>
                <li><Link className="mobile-nav-link" to="/reservas">RESERVAS</Link></li>
                <li><Link className="mobile-nav-link" to="/contacto">CONTACTO</Link></li>
            </ul>
        </div>
    )
}

export default MobileNav